class Home extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			seconds: 0,
		}
		setInterval(() => {
			this.setState({
				seconds: this.state.seconds + 1,
			});
		}, 1000)
	}

	render() {
	  return (
		<div>

			Once there was a dylexic agnostic, wondering if there really was a dog{".".repeat(this.state.seconds)}
		</div>
	)
	}
}


export default Home
